import {
  FooterWrapper,
  NavigationWrapper,
  SocialMediaWrapper,
  SocialLinksWrapper,
  RegistrationWrapper,
  NavigationColumnWrapper,
  NavigationColumnDescription,
  RegistrationDescription,
} from './styles';
import { FOOTER_NAVIGATION, SOCIAL_MEDIA_LINKS } from './data';
import { Title } from 'ui';

export const Footer = () => (
  <FooterWrapper>
    <NavigationWrapper>
      {FOOTER_NAVIGATION.map(({ title, navigation }, index) => (
        <NavigationColumnWrapper key={index}>
          <Title variant='secondary' heading='h4'>
            {title}
          </Title>
          {navigation.map(({ label }) => (
            <NavigationColumnDescription key={Math.random()}>
              {label}
            </NavigationColumnDescription>
          ))}
        </NavigationColumnWrapper>
      ))}
    </NavigationWrapper>
    <SocialLinksWrapper>
      <Title variant='secondary'>IThub</Title>
      <SocialMediaWrapper>
        {SOCIAL_MEDIA_LINKS.map(({ Icon }, index) => (
          <Icon key={index} />
        ))}
      </SocialMediaWrapper>
    </SocialLinksWrapper>
    <RegistrationWrapper>
      <RegistrationDescription>
         {new Date().getFullYear()},   
          &quot; 
          &quot; &quot; . . . 
         , . , . 3, . 4, / 3/1,
        . 39 ,  105120
      </RegistrationDescription>
    </RegistrationWrapper>
  </FooterWrapper>
);
